import { createSlice } from '@reduxjs/toolkit'


let frankieDependents = {}


export const frankieStateEngine = createSlice({
  name: 'runState',
  initialState: { state: {} },
  reducers: {
    valueChange: (state, action) => {
      state = setValueInState(state, action.payload)
    },
    stateReceived: (state, action) => {
      frankieDependents = action.payload.state.frankieDependents;
      state.navigation = undefined
      let keys = Object.keys(state)
      keys.forEach(key => delete state[key])
      let newState = action.payload.state.frankieVariables
      keys = Object.keys(newState)
      keys.forEach(key => state[key] = newState[key])
    },
    langChange: (state, action) => {
      state.navigation = {
        values: getValues(state),
        lang: action.payload.lang,
        navigationDirection: { "name": "RESUME", skipInvalid: !state.Survey.show_errors }
      }
    },
    navigateNext: (state) => {
      next(state)
    },
    navigatePrevious: (state) => {
      state.navigation = {
        navigationDirection: { "name": "PREV" }
      }
    },
    jump: (state, action) => {
      state.navigation = {
        values: getValues(state),
        navigationDirection: { "name": "JUMP", navigationIndex: action.payload, skipInvalid: true }
      }
    }
  }
})

export const { valueChange, stateReceived, langChange, navigateNext, navigatePrevious, jump } = frankieStateEngine.actions

export default frankieStateEngine.reducer

function onDependencyChanged(state, componentName, variableName, newValue, source) {
  if (typeof state[componentName] === 'undefined') {
    return;
  }

  if (state[componentName][variableName] === newValue) {
    console.log("same value - " + componentName + "." + variableName + ": " + newValue)
  } else {
    console.log(componentName + "." + variableName + ": " + JSON.stringify(newValue) + " due to " + source)
    state[componentName][variableName] = newValue;
    getDependents(componentName, variableName).forEach(dependent => {
      onDependencyChanged(state, dependent[0], dependent[1], window.frankieRuntime[dependent[0]][dependent[1]](state), componentName + "." + variableName)
    });
  }
}

function getDependents(componentName, variableName) {
  if (typeof frankieDependents[componentName] !== 'undefined' && typeof frankieDependents[componentName][variableName] !== 'undefined') {
    return frankieDependents[componentName][variableName];
  } else {
    return [];
  }
}

function setValueInState(state, payload) {
  let componentCode = payload.componentCode
  let value = payload.value
  let element = state[componentCode]
  let newState = { ...state }
  if (typeof element !== 'undefined' && element["value"] !== value) {
    let time = Date.now()
    onDependencyChanged(newState, componentCode, "value", value, "VALUE CHANGE")
    console.log("NEW STATE in: " + (Date.now() - time) + " millis")
    return newState;
  }
  return state;
}

function next(state) {
  if (!state.Survey.validity && !state.Survey.show_errors) {
    state.Survey.show_errors = true
  } else {
    state.navigation = {
      values: getValues(state),
      navigationDirection: { "name": "NEXT", skipInvalid: true }
    }
  }
}

function getValues(frankieState) {
  let values = {}
  for (var key in frankieState) {
    if (frankieState.hasOwnProperty(key)) {
      let element = frankieState[key]
      if (element.hasOwnProperty("value")) {
        let value = element["value"]
        if (typeof value !== 'undefined') {
          values[key + ".value"] = value
        }

      }
    }
  }
  return values
}
